<template>
  <div class="step-content-barber grey lighten-1 pa-4 app-border-radius-small">
    <div class="d-flex align-center">
      <v-switch
        v-model="chooseBarber"
      ></v-switch>
      <span class="pl-6">
        Escolher barbeiro?
      </span>
    </div>
    <v-card
      :disabled="!chooseBarber"
      class="mt-4 elevation-1 pa-2"
    >
      <div
        :key="barber.id"
        v-for="(barber, index) in barbers"  
      >
        <v-divider v-if="index > 0"></v-divider>
        <barber-item 
          :barber="barber"
          @toggle="setBarber"
        />
      </div>
    </v-card>
  </div>
</template>

<script>
import BarberItem from './item/Item'
import BarberMixin from '@/mixins/BarberMixin'
import SnackbarMixin from '@/mixins/SnackbarMixin'

export default {
  name: 'Barbeiro',
  components: { BarberItem },
  mixins: [ BarberMixin, SnackbarMixin ],
  props: {
    barbershopId: {
      default: null
    }
  },
  data: () => ({
    barbers: [],
    chooseBarber: true
  }),
  watch: {
    barbershopId (value) {
      if (value) this.getBarberByBarbershopIdOnService(value)
    },
    chooseBarber (newValue) {
      this.$emit('setChooseBarber', newValue)
    }
  },
  mounted () {
    if (this.barbershopId) this.getBarberByBarbershopIdOnService(this.barbershopId)
  },
  methods: {
    onGetBarberByBarbershopIdOnServiceError (err) {
      this.showSnackbarErro(err)
    },
    onGetBarberByBarbershopIdOnServiceSuccess (response) {
      this.barbers = response
    },
    setBarber (barber) {
      let index = this.barbers.findIndex(i => i.id == barber.id)
      this.barbers[index] = barber
      let barbers = this.barbers.filter(i => i.choosed == true)
      this.$emit('changed', barbers)
    }
  }
}
</script>