<template>
  <v-row>
    <v-col cols="auto" class="py-0">
      <div class="barber-item-image">
        <v-img
          :src="`${$url_backend}/storage/uploads/barbers/profile/2a693634-591c-46dd-99c8-781e23be112a/2a693634-591c-46dd-99c8-781e23be112a54.png`"
          :lazy-src="`${$url_backend}/storage/uploads/barbers/profile/2a693634-591c-46dd-99c8-781e23be112a/2a693634-591c-46dd-99c8-781e23be112a54.png`"
          aspect-ratio="1"
          class="grey lighten-2"
        ></v-img>
      </div>
    </v-col>
    <v-col class="py-0 d-flex align-center">
      <span class="font-weight-bold">
        {{ barber.name }}
      </span>

      <v-checkbox
        class="mt-2 d-sm-none"
        v-model="choosed"
      ></v-checkbox>
    </v-col>
    <v-col cols="auto" class="d-none d-sm-flex">
      <v-checkbox
        class="mt-2"
        v-model="choosed"
      ></v-checkbox>
    </v-col>
  </v-row>
</template>

<script>
import './Item.scss'

export default {
  name: 'Item',
  props: {
    barber: {
      required: true
    }
  },
  computed: {
    choosed: {
      set (newValue) {
        this.barber.choosed = newValue
        this.$emit('toggle', this.barber)
      },
      get () {
        return this.barber.choosed
      }
    }
  }
}
</script>