<template>
  <div class="step-time">
    <v-row>
      <v-col cols="auto" class="py-0">
        <date-picker
          label="Data do agendamento"
          :max="maxDate"
          :min="minDate"
          @change="dateChanged"
         />
      </v-col>
    </v-row>
    
    <v-row v-if="times.length > 0">
      <v-col
        :key="index"
        v-for="(timeItem, index) in times"
        cols="auto"
        @click="chooseTime(timeItem)"
      >
        <div
          class="grey lighten-1 pa-2 text-center app-border-radius-small step-time-item"
          :class="{ active: timeItem === time }"
        >
          {{ formatResponseToTime(timeItem) }}
        </div>
      </v-col>
    </v-row>
    <p class="d-flex justify-center align-center text-center" v-else>
      Não há horários disponíveis para agendamento
    </p>
  </div>
</template>

<script>
import Axios from '@/plugins/axios'
import './Time.scss'
import DatePicker from '@/components/globais/form/date-picker/DatePicker'
import DateMixin from '@/mixins/DateMixin'
import TimeMixin from '@/mixins/TimeMixin'
import BarbershopMixin from '@/mixins/BarbershopMixin'

export default {
  name: 'Time',
  mixins: [ BarbershopMixin, DateMixin, TimeMixin ],
  components: { DatePicker },
  props: {
    barbershop_id: { required: true },
    duration: { default: '0:00' }
  },
  data: () => ({
    barbershopObj: null,
    time: null,
    times: [],
    date: null,
    loading: true
  }),
  mounted () {
    this.dates = new Date()
    this.getTimes(this.date)
  },
  computed: {
    maxDate () {
      let maxDate = new Date()
      maxDate.setDate(maxDate.getDate() + 6)
      return maxDate.toISOString().substr(0, 10)
    },
    minDate () {
      let minDate = new Date()
      minDate.setDate(minDate.getDate() - 1)
      return minDate.toISOString().substr(0, 10)
    }
  },
  watch: {
    date (val) {
      if (val) this.getTimes(val)
    }
  },
  methods: {
    chooseTime (time) {
      this.time = time
      let [ hours, minutes ] = this.time.split(':') 
      this.date.setHours(hours, minutes, 0)
      this.$emit('choosed', this.date)
    },
    dateChanged (newDate) {
      let date = new Date(newDate)
      date.setDate(date.getDate() + 1)
      this.date = date
    },
    getTimes (date) {
      date = this.formatDateToDateEn(date)
      this.times = []
      Axios.get(`/barbershop/${this.barbershop_id}/time/available/${date}`)
      .then(response => {
        if (response.data.codigo === 200) this.times = response.data.corpo  
        else this.times = []
      })
      .catch ((err) => {
        this.times = []
        window.console.log(err)
      })
    }
  }
}
</script>