<template>
  <v-stepper-content :step="step" class="step-content">
    <v-row class="mb-4">
      <v-col cols="12">
        <div class="step-content-box">
          <slot></slot>
        </div>  
      </v-col>
    </v-row>

    <btn-secondary
      :block="false"
      :text="btnConfirmText" 
      @clicked="confirm"
      :disabled="disabled"
    />

    <v-btn
      text
      @click="cancel"
    >
      Cancel
    </v-btn>
  </v-stepper-content>
</template>

<script>
import './StepContent.scss'
import BtnSecondary from '@/components/globais/buttons/secondary/BtnSecondary'

export default {
  name: 'StepContent',
  components: { BtnSecondary },
  props: {
    btnConfirmText: {
      default: 'Continuar'
    },
    step: {
      required: true
    },
    disabled: {
      default: true
    },
    confirmSchedule: {
      default: false
    }
  },
  methods: {
    cancel () {
      this.$emit('cancel')
    },
    confirm () {
      this.$emit('clicked')
    }
  }
}
</script>